<template>
  <div>
    <!--class 두개 이상 바인딩시킬때 표현 : 배열식-->
    <div class="container" :class="[activeClass, errorClass]">
      클래스 바인딩
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeClass: "active", 
      errorClass: "text-red",  
    };
  },
};
</script>

<style scoped>
.container {
  width: 100%;
  height: 200px;
}

.active {
  background-color: rgb(153, 205, 239);
  font-weight: bold;
}

.text-red {
  color: red;
}
</style>